<template>
    <div class="bottom-button" :class="{left:bottomType==='left'}">
        <div class="main-button" @click="changeMenuOpen" :style="bottomStyle">
            <div class="main-icon" :class="{active:openMenu}"><i class="el-icon-plus"></i></div>
        </div>
        <div class="item-box" :class="{active:openMenu}">
            <div class="menu-item" v-for="item in mobileConfig.menuList" :style="itemStyle" @click="clickItem(item)">
                <diy-image :src="(item.linkType === 'shopCart' && $route.name === 'cart') || (item.linkType === 'userCenter' && $route.name === 'home') ? item.selectIcon : item.icon" class="menu-icon"></diy-image>
                <div class="menu-txt" :style="{color:(item.linkType === 'shopCart' && $route.name === 'cart') || (item.linkType === 'userCenter' && $route.name === 'home') ? selectFontColor : ''}">{{ $L(item.text) }}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import DiyImage from "../../../../common/diyImage";
    export default {
        name: "bottomButton",
        components: {DiyImage},
        props:{
            bottomMenu:{
                type:Object,
                default:()=>{
                    return {};
                }
            }
        },
        data() {
            let mobileConfig = this.bottomMenu;
            let bottomStyle = '';
            let bottomType = 'left';
            let itemStyle = '';
            let selectFontColor = '';
            if (mobileConfig) {
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('backgroundColor') && mobileConfig.styleSettings.backgroundColor !== '') {
                    itemStyle += 'background-color:' + mobileConfig.styleSettings.backgroundColor + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontFamily') && mobileConfig.styleSettings.fontFamily !== '') {
                    itemStyle += 'font-family:' + mobileConfig.styleSettings.fontFamily + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontSize') && mobileConfig.styleSettings.fontSize !== '') {
                    itemStyle += 'font-size:' + mobileConfig.styleSettings.fontSize + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontWeight') && mobileConfig.styleSettings.fontWeight === true) {
                    itemStyle += 'font-weight:600;';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontStyle') && mobileConfig.styleSettings.fontStyle === true) {
                    itemStyle += 'font-style:italic;';
                }
                let textDecoration = '';
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontUnderline') && mobileConfig.styleSettings.fontUnderline === true) {
                    textDecoration += ' underline ';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontLineThrough') && mobileConfig.styleSettings.fontLineThrough === true) {
                    textDecoration += ' line-through ';
                }
                if (textDecoration !== '') {
                    itemStyle += 'text-decoration:' + textDecoration + ';';
                }
                //选择状态下的字体颜色
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('selectFontColor') && mobileConfig.styleSettings.selectFontColor !== '') {
                    selectFontColor = mobileConfig.styleSettings.selectFontColor;
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('buttonColor') && mobileConfig.styleSettings.buttonColor !== '') {
                    bottomStyle += 'background-color:' + mobileConfig.styleSettings.buttonColor + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('buttonPosition') && mobileConfig.styleSettings.buttonPosition !== 'left') {
                    bottomType = 'right';
                }
            }
            return {
                mobileConfig: mobileConfig,
                bottomStyle: bottomStyle,
                bottomType: bottomType,
                itemStyle: itemStyle,
                selectFontColor: selectFontColor,
                openMenu: false
            }
        },
        methods: {
            changeMenuOpen(){
                if(this.openMenu){
                    this.openMenu = false;
                }else{
                    this.openMenu = true;
                }
            },
            clickItem(item) {
                if (item && item.hasOwnProperty('linkType')) {
                    this.changeMenuOpen();
                    switch (item.linkType) {
                        case 'userCenter':
                            this.$router.push({name: 'home'});
                            break;
                        case 'shopCart':
                            this.$router.push({name: 'cart'});
                            break;
                        case 'home':
                            this.$gotoWebHome();
                            break;
                        case 'sms':
                            //发送短信
                            if (item.hasOwnProperty('linkVal') && item.linkVal !== '') {
                                window.location.href = 'sms:' + item.linkVal;
                            }
                            break;
                        case 'phone':
                            //打电话
                            if (item.hasOwnProperty('linkVal') && item.linkVal !== '') {
                                window.location.href = 'tel:' + item.linkVal;
                            }
                            break;
                        case 'link':
                            //跳转链接
                            if (item.hasOwnProperty('linkVal') && item.linkVal !== '') {
                                window.top.location.href = item.linkVal;
                            }
                            break;
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../../assets/css/common/lib.scss';

    .bottom-button {
        position: fixed;
        z-index: 300;
        right: 10px;
        bottom: 10px;
        width: 45px;
        height: 45px;

        .main-button{
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: #000000;
            border-radius: 50%;
            top: 0;
            left: 0;
            z-index: 100;

            .main-icon{
                position: absolute;
                width: 100%;
                height: 100%;
                line-height: 45px;
                text-align: center;
                color: #ffffff;
                font-size: 28px;

                @include diy-animation-show(all);

                &.active{
                    transform: rotate(225deg);
                }
            }
        }

        .item-box{
            position: absolute;
            width: 100%;
            bottom: 45px;
            left: 0;
            overflow: hidden;
            height: 0;
            @include diy-animation-show(all);
            opacity: 0;

            &.active{
                height: unset;
                overflow: unset;
                opacity: 1;
            }

            .menu-item{
                position: relative;
                width: 45px;
                height: 45px;
                border-radius: 45px;
                text-align: center;
                line-height: 45px;
                margin-bottom: 20px;

                .menu-icon{
                    width: 50%;
                    height: 50%;
                    position: absolute;
                    top: 40%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                    /deep/ .el-image__inner {height: 100%}
                }

                .menu-txt{
                    position: absolute;
                    top: 50%;
                    right: 55px;
                    background-color: #ffffff;
                    line-height: 30px;
                    padding: 0 20px;
                    white-space: nowrap;
                    transform: translateY(-50%);
                    box-shadow: #ddd 2px 2px 12px;
                }
            }
        }

        &.left{
            left: 10px;
            right: unset;

            .menu-txt{
                left: 55px;
                right: unset!important;
            }
        }
    }
</style>
