<template>
    <div class="bottom-line"
         :style="bottomStyle"
         v-if="mobileConfig && mobileConfig.hasOwnProperty('menuList') && mobileConfig.menuList.length > 0">
        <div class="bottom-item"
             v-for="(item,index) in mobileConfig.menuList"
             @click="clickItem(item,index)"
             :class="{icon:showIcon && !showTxt,txt:!showIcon && showTxt}"
             :style="{
                width:(100/mobileConfig.menuList.length)+'%',
                color:(item.linkType === 'shopCart' && $route.name === 'cart') || (item.linkType === 'userCenter' && $route.name === 'home') ? selectFontColor : ''}">
            <diy-image class="item-icon"
                       fit="scale-down"
                       v-if="showIcon"
                       :src="(item.linkType === 'shopCart' && $route.name === 'cart') || (item.linkType === 'userCenter' && $route.name === 'home') ? item.selectIcon : item.icon"></diy-image>
            <div class="item-text" v-if="showTxt">
                <span class="item-text-val">{{ $L(item.text) }}</span>
            </div>
            <div class="menu-children" :class="{active:index===nowSelectMenu}" v-if="item.linkType === 'menu' && item.hasOwnProperty('linkVal') ">
                <div class="children-item" v-for="(ch,ch_k) in item.linkVal" @click="clickItem(ch,ch_k)">{{ch.text}}</div>
            </div>
        </div>
    </div>
</template>

<script>
    import DiyImage from "../../../../common/diyImage";
    export default {
        name: "bottomLine",
        components: {DiyImage},
        props:{
            bottomMenu:{
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        data() {
            let mobileConfig = this.bottomMenu;
            let bottomStyle = '';
            let selectFontColor = '';
            let showIcon = true;
            let showTxt  = true;
            if (mobileConfig) {

                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('styleType')) {
                    //图标状态：1图标+文字，2纯文字，3纯图标，4按钮样式
                    if(mobileConfig.styleSettings.styleType == 2){
                        showIcon = false;
                        showTxt  = true;
                    }else if(mobileConfig.styleSettings.styleType == 3){
                        showIcon = true;
                        showTxt  = false;
                    }else{
                        showIcon = true;
                        showTxt  = true;
                    }
                }

                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('height') && mobileConfig.styleSettings.height !== '') {
                    bottomStyle += 'height:' + mobileConfig.styleSettings.height + ';top:calc(100% - ' + mobileConfig.styleSettings.height + ');';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('backgroundColor') && mobileConfig.styleSettings.backgroundColor !== '') {
                    bottomStyle += 'background-color:' + mobileConfig.styleSettings.backgroundColor + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontFamily') && mobileConfig.styleSettings.fontFamily !== '') {
                    bottomStyle += 'font-family:' + mobileConfig.styleSettings.fontFamily + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontSize') && mobileConfig.styleSettings.fontSize !== '') {
                    bottomStyle += 'font-size:' + mobileConfig.styleSettings.fontSize + ';';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontWeight') && mobileConfig.styleSettings.fontWeight === true) {
                    bottomStyle += 'font-weight:600;';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontStyle') && mobileConfig.styleSettings.fontStyle === true) {
                    bottomStyle += 'font-style:italic;';
                }
                let textDecoration = '';
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontUnderline') && mobileConfig.styleSettings.fontUnderline === true) {
                    textDecoration += ' underline ';
                }
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('fontLineThrough') && mobileConfig.styleSettings.fontLineThrough === true) {
                    textDecoration += ' line-through ';
                }
                if (textDecoration !== '') {
                    bottomStyle += 'text-decoration:' + textDecoration + ';';
                }
                //选择状态下的字体颜色
                if (mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('selectFontColor') && mobileConfig.styleSettings.selectFontColor !== '') {
                    selectFontColor = mobileConfig.styleSettings.selectFontColor;
                }
            }
            return {
                mobileConfig: mobileConfig,
                bottomStyle: bottomStyle,
                selectFontColor: selectFontColor,
                nowSelectMenu:null,
                showIcon:showIcon,
                showTxt:showTxt
            }
        },
        methods:{
            clickItem(item,index){
                if(item && item.hasOwnProperty('linkType')){
                    switch (item.linkType) {
                        case 'userCenter':
                            this.$router.push({name:'home'});
                            break;
                        case 'shopCart':
                            this.$router.push({name:'cart'});
                            break;
                        case 'home':
                            this.$gotoWebHome();
                            break;
                        case 'sms':
                            //发送短信
                            if(item.hasOwnProperty('linkVal') && item.linkVal !==''){
                                window.location.href = 'sms:' + item.linkVal;
                            }
                            break;
                        case 'phone':
                            //打电话
                            if(item.hasOwnProperty('linkVal') && item.linkVal !==''){
                                window.location.href = 'tel:' + item.linkVal;
                            }
                            break;
                        case 'link':
                            //跳转链接
                            if(item.hasOwnProperty('linkVal') && item.linkVal !==''){
                                window.top.location.href = item.linkVal;
                            }
                            break;
                        case 'menu':
                            //显示子菜单
                            if(index===this.nowSelectMenu){
                                this.nowSelectMenu = null;
                            }else{
                                this.nowSelectMenu = index;
                            }
                            break;
                    }
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../../assets/css/common/lib.scss';
    .bottom-line {
        position: absolute;
        width: 100%;
        height: 50px;
        /*防止输入法顶上去*/
        top: calc(100% - 50px);
        left: 0;
        z-index: 90;
        border-top: 1px solid #DCDFE6;
        font-size: 12px;

        .bottom-item {
            position: relative;
            height: 100%;
            float: left;
            text-align: center;

            .item-icon {
                position: relative;
                height: 60%;
                z-index: 90;
                padding-top: 2px;

                .error-img {
                    position: absolute;
                    font-size: 180%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
                /deep/ .el-image__inner {height: 100%}
            }

            .item-text {
                position: absolute;
                top: 60%;
                left: 0;
                height: 40%;
                width: 100%;
                z-index: 100;

                .item-text-val {
                    position: absolute;
                    width: 100%;
                    line-height: 100%;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    text-align: center;
                }
            }

            &.icon{
                .item-icon{
                    height: 80%;
                    top: 10%;
                }
            }

            &.txt{
                .item-text{
                    top: 0;
                    height: 100%;
                }
            }

            .menu-children{
                position: absolute;
                left: 0;
                top: 50px;
                width: 100%;
                background-color: #ffffff;
                @include diy-animation-show(all);
                opacity: 0;
                .children-item{
                    line-height: 35px;
                    text-align: center;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                    font-size: 12px;
                    color: #606266;
                    border: 1px solid #E4E7ED;
                    border-bottom: none;
                    z-index: 10;
                    &:active{
                        color: #909399;
                    }
                }
                &.active{
                    top: unset;
                    bottom: 50px;
                    opacity: 1;
                }
            }
        }
    }
</style>
