<template>
    <div class="top-view">
        <div class="left-btn" @click="goBack">
            <i class="el-icon-arrow-left"></i>
        </div>
        <span class="title">{{ mobileTitle }}</span>
        <div class="right-btn-list" v-if="mobileTopBtn && mobileTopBtn.length > 0">
            <div class="right-btn-item" v-for="item in mobileTopBtn"
                 @click="typeof item.func === 'function' ? item.func() : null">
                <span class="bbx-font" :class="item.icon" v-if="item.hasOwnProperty('icon')"
                      :style="{'font-size':item.hasOwnProperty('size')?item.size:''}"></span>
                <span class="show-txt mainFontColor" v-if="item.hasOwnProperty('text')"
                      :style="{'font-size':item.hasOwnProperty('size')?item.size:''}">{{ item.text }}</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "topView",
        computed: {
            //显示标题
            mobileTitle: {
                get() {
                    return this.$store.state.mobileTitle;
                },
                set(val) {
                    this.$store.commit('setMobileTitle', val);
                }
            },
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        methods: {
            goBack() {
                if (this.$route.name === "cart") {
                    this.$router.go(-1);
                } else if (this.$in_array(this.$route.name, [
                    'distributorListMobile',
                    'distributorOrderMobile',
                    'distributorUserOrderMobile',
                    'distributorWithdrawalMobile',
                    'distributorRankingMobile',
                    'applyWithdrawalMobile',
                    'distributorSettingsMobile',
                    'promotionSettingsMobile',
					'distributorMemberMobile',
					'memberOrderMobile',
                    'distributorModifyMobile',
                ])) {
                    this.$router.push({name: 'distributorMain'});
                }else if(this.$in_array(this.$route.name, [
                    'distributorDetailsMobile',
                ])){
					this.$router.push({name: 'distributorListMobile'});
				}
                else if(this.$in_array(this.$route.name, [
                    'distributorEditModifyMobile',
                ])){
                    this.$router.push({name: 'distributorModifyMobile'});
                }else if (this.$in_array(this.$route.name, [
                    'promotionSettingsChooseMobile',
                    'promotionSettingsEditMobile',
                ])){
                    this.$router.push({name: 'promotionSettingsMobile'});
                }else if(
                    this.$in_array(this.$route.name, [
                        'agentListMobile',
                        'agentOrderMobile',
                        'agentUserOrderMobile',
                        'agentWithdrawalMobile',
                        'agentRankingMobile',
                        'agentApplyWithdrawalMobile',
                        'agentSettingsMobile',
                        'agentAromotionSettingsMobile',
                    ])){
                    this.$router.push({name: 'agentMains'});
                }else if (this.$in_array(this.$route.name, [
                    'agentPromotionSettingsChooseMobile',
                    'agentPromotionSettingsEditMobile',
                ])){
                    this.$router.push({name: 'agentPromotionSettingsMobile'});
                }else if(this.$in_array(this.$route.name, [
                    'activeCard',
                    'detailsMobile',
					'recordMobile',
                ])){
					this.$router.push({name: 'userCardMobile'});
				}else if(this.$in_array(this.$route.name, [
                    'payCharge',
                ])){
					this.$router.push({name: 'payRecord'});
				}else if(this.$in_array(this.$route.name, [
                    'messageInfo',
                ])){
                    this.$router.push({name: 'messageList'});
                }else if (this.$in_array(this.$route.name, [
                    'orderInfo',
                    'orderService',
                    'orderServiceInfo',
                    'orderComment',
                    'checkComment'
                ])) {
                    this.$router.push({name: 'orderList',params:{type:'all'}});
                } else {
                    this.$router.push({name: 'home'});
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../../assets/css/common/lib.scss';

    .top-view {
        text-align: center;
        color: #303133;
        background-color: #ffffff;
        border-bottom: 1px solid #DCDFE6;
        font-size: 18px;
        @extend .no-select;

        .title{
            vertical-align: middle;
        }

        .left-btn {
            position: absolute;
            width: 35px;
            height: 100%;
            text-align: center;
            top: 0;
            left: 0;
            z-index: 100;
            font-size: 24px;
            @include diy-animation-show(color);
            color: #909399;
            vertical-align: middle;

            &:active {
                color: #C0C4CC;
            }
        }

        .right-btn-list {
            position: absolute;
            height: 100%;
            top: 0;
            right: 0;
            z-index: 99;

            .right-btn-item {
                position: relative;
                width: 38px;
                height: 100%;
                text-align: center;
                float: left;
                @include diy-animation-show(color);
                color: #909399;

                &:active {
                    opacity: 0.8;
                }

                .bbx-font{
                    font-size: 24px;
                    vertical-align: middle;
                }

                .show-txt {
                    font-size: 14px;
                    vertical-align: middle;
                }
            }
        }
    }
</style>