<template>
    <div class="main-box" v-loading="loading">
        <top-view v-if="!loading && showTop"
                  class="top"></top-view>
        <div class="main" v-if="!loading" :class="{ showTop:showTop }">
            <div class="center" :style="showBottom ? centerStyle : ''">
                <vue-scroll>
                    <router-view></router-view>
                </vue-scroll>
            </div>
            <component :is="bottomView" v-if="showBottom && bottomView" :bottom-menu="mobileConfig"></component>
        </div>
    </div>
</template>

<script>
    import topView from "./mainBoxChildren/topView";
    import bottomLine from "./mainBoxChildren/bottomView/bottomLine";
    import bottomButton from "./mainBoxChildren/bottomView/bottomButton";

    export default {
        name: "mainBox",
        components: {topView},
        data() {


            return {
                heartbeatTime: 10000,
                heartbeatObj: null,
                loading: true,
                mobileConfig: {},
                bottomView: null,
                centerStyle: {},
            }
        },
        created() {
            if (this.$route.query.hasOwnProperty('lang')) {
                let lang = this.$route.query.lang;
                lang = parseInt(lang);
                if (!isNaN(lang)) {
                    this.$lang('lang_' + lang);
                }
            }
            this.getLoginConfig();
        },
        provide() {
            return {
                cleanHeartbeat: this.cleanHeartbeat,
            }
        },
        beforeRouteEnter(to, from, next) {
            // 在渲染该组件的对应路由被 confirm 前调用
            // 不！能！获取组件实例 `this`
            // 因为当守卫执行前，组件实例还没被创建
            next(vm => {
                // 通过 `vm` 访问组件实例
                vm.setSeo(to.name);
            });
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.setSeo(to.name);
            next();
        },
        computed: {
            //是否显示头部
            showTop: {
                get() {
                    if (this.$route.name !== 'home' && this.$route.name !== 'login') {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            //是否显示底部
            showBottom: {
                get() {
                    if (this.$route.name === 'home' || this.$route.name === 'cart') {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            //不能进入的栏目
            menuClose: {
                get() {
                    if (this.$store.state.loginConfig.hasOwnProperty('settings') && this.$store.state.loginConfig.settings.hasOwnProperty('menu') && this.$store.state.loginConfig.settings.menu.hasOwnProperty('close')) {
                        return this.$store.state.loginConfig.settings.menu.close;
                    } else {
                        return [];
                    }
                }
            },
            //购物车数量
            cartNum: {
                get() {
                    return this.$store.state.heartbeatData.hasOwnProperty('cartNum') ? this.$store.state.heartbeatData.cartNum : 0;
                },
                set(cartNum) {
                    this.$store.commit('setHeartbeatData', {
                        cartNum: cartNum
                    })
                }
            },
            //显示标题
            mobileTitle: {
                get() {
                    return this.$store.state.mobileTitle;
                },
                set(val) {
                    this.$store.commit('setMobileTitle', val);
                }
            },
            //显示标题
            seoTitle: {
                get() {
                    return this.$store.state.seoTitle;
                },
                set(val) {
                    this.$store.commit('changeSeo', {seoTitle: val});
                }
            }
        },
        methods: {
            setSeo(router) {
                if (this.loading) {
                    return false;
                }
                if (!router) {
                    router = this.$route.name;
                }
                let menu = this.$C('menuList');
                let seoTitle = this.$L('会员中心');
                let routerId = null;
                for (let key in menu) {
                    if (menu[key]) {
                        if (menu[key].hasOwnProperty('showRouter') && this.$in_array(router, menu[key].showRouter) && menu[key].hasOwnProperty('name')) {
                            this.mobileTitle = this.$L(menu[key].name);
                            seoTitle = this.$L(menu[key].name) + ' - ' + seoTitle;
                            routerId = menu[key].id;
                            break;
                        }
                    }
                }
                if (router === 'cart') {
                    this.mobileTitle = this.$L('购物车');
                    seoTitle = this.mobileTitle + ' - ' + seoTitle;
                } else if (routerId === null || this.$in_array(routerId, this.menuClose)) {
                    this.$router.push({name: 'home'});
                }

                let webName = this.$C('webName');
                if (webName && webName !== '') {
                    if (seoTitle === '') {
                        seoTitle = this.$L(webName);
                    } else {
                        seoTitle = seoTitle + ' - ' + this.$L(webName);
                    }
                }
                this.seoTitle = seoTitle;
            },
            setupConfig() {

                //直接请求底部导航api接口
                const _self = this;

                _self.$api('getBottomMenu', {}, function (reData) {
                    if (reData.flag === 1) {
                        let mobileConfig = [];
                        let bottomView = null;
                        let centerStyle = '';
                        if (reData.data.hasOwnProperty('mobileConfig')) {
                            mobileConfig = reData.data.mobileConfig;
                            if (mobileConfig && mobileConfig.hasOwnProperty('styleSettings') && mobileConfig.styleSettings.hasOwnProperty('styleType')) {
                                if (mobileConfig.styleSettings.styleType === 4) {
                                    bottomView = bottomButton;
                                    centerStyle = 'height:100%';
                                } else {
                                    bottomView = bottomLine;
                                    let bottomHeight = '50px';
                                    if (mobileConfig.styleSettings.hasOwnProperty('height') && mobileConfig.styleSettings.height !== '') {
                                        bottomHeight = mobileConfig.styleSettings.height;
                                    }
                                    centerStyle = 'height:calc(100% - ' + bottomHeight + ')';
                                }
                            }
                        }

                        _self.mobileConfig = mobileConfig;
                        _self.bottomView = bottomView;
                        _self.centerStyle = centerStyle;
                        _self.loading = false;
                        _self.setSeo();
                        _self.heartbeat();

                    } else if (reData.flag === -1) {
                        _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                    } else {
                        _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                            confirmButtonText: _self.$L('点击重试'),
                            type: 'error',
                            showClose: false
                        }).then(() => {
                            _self.getLoginConfig(true);
                        });
                    }
                }, function (error) {
                    console.log(error);
                    _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                        confirmButtonText: _self.$L('点击重试'),
                        type: 'error',
                        showClose: false
                    }).then(() => {
                        _self.getLoginConfig();
                    });
                });



            },
            getLoginConfig() {
                const _self = this;
                _self.$api('getLoginConfig', {}, function (reData) {
                    if (reData.flag === 1) {
                        if (reData.data.hasOwnProperty('webConfig')) {
                            _self.$store.commit('setServerConfig', {
                                systemConfig: reData.data.webConfig
                            });
                            delete reData.data.webConfig;
                        }
                        _self.$store.commit('setLoginConfig', reData.data);
                        _self.setupConfig();
                    } else if (reData.flag === -1) {
                        _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                    } else {
                        _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                            confirmButtonText: _self.$L('点击重试'),
                            type: 'error',
                            showClose: false
                        }).then(() => {
                            _self.getLoginConfig(true);
                        });
                    }
                }, function (error) {
                    console.log(error);
                    _self.$alert(_self.$L('服务器无响应'), _self.$L('网络请求失败'), {
                        confirmButtonText: _self.$L('点击重试'),
                        type: 'error',
                        showClose: false
                    }).then(() => {
                        _self.getLoginConfig();
                    });
                });
            },
            heartbeat() {
                const _self = this;
                _self.cleanHeartbeat();
                let VueUserCenterTruePublish = localStorage.getItem('VueUserCenterTruePublish');
                _self.$api('heartbeat', {
                    router: _self.$route.name,
                    version: _self.$C('version'),
                    versionNum: _self.$C('versionNum'),
                    publish: VueUserCenterTruePublish && VueUserCenterTruePublish === 'true' ? 1 : 0
                }, function (reData) {
                    if (reData.flag === 1) {
                        //执行购物车的刷新
                        if (reData.data.hasOwnProperty('cartNum')) {
                            _self.cartNum = reData.data.cartNum;
                        }
                        //console.log('心跳正常',new Date().toLocaleTimeString());
                        _self.heartbeatObj = setTimeout(() => {
                            _self.heartbeat();
                        }, _self.heartbeatTime);
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                    } else {
                        console.log("心跳失败", new Date().toLocaleTimeString());
                        console.log(reData);
                        _self.heartbeatObj = setTimeout(() => {
                            _self.heartbeat();
                        }, _self.heartbeatTime);
                    }
                }, function (error) {
                    console.log("心跳失败", new Date().toLocaleTimeString());
                    console.log(error);
                    _self.heartbeatObj = setTimeout(() => {
                        _self.heartbeat();
                    }, _self.heartbeatTime);
                }, false);
            },
            cleanHeartbeat() {
                if (this.heartbeatObj) {
                    clearTimeout(this.heartbeatObj);
                    //console.log("cleanHeartbeat",new Date().toLocaleTimeString());
                }
            }
        },
        beforeDestroy() {
            this.cleanHeartbeat();
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';

    .main-box {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
        overflow: hidden;

        .top {
            position: absolute;
            width: 100%;
            height: 50px;
            line-height: 50px;
            top: 0;
            left: 0;
            z-index: 100;
        }

        .main {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 80;

            &.showTop {
                top: 50px;
                height: calc(100% - 50px);
            }

            .center {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                z-index: 80;
                .__vuescroll{
                	/*-webkit-overflow-scrolling: touch;*/
                }
                /deep/ .__panel{
                	-webkit-overflow-scrolling: touch;
                }
            }
        }
    }
</style>
